@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700;800&display=swap");
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  margin: 0;
  color: #5A5C69;
}

p {
  color: #5A5C69;
  font-size: 2rem;
  margin: 0;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Nunito", sans-serif;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

ul {
  margin: 0;
  padding: 0;
  text-decoration: none;
  overflow-anchor: none;
}
ul li {
  margin: 0;
  padding: 0;
  list-style: none;
}
ul li span {
  font-weight: 900;
  color: #FFFFFF;
}

.flex {
  display: flex;
  position: relative;
}
.flex .navbar-nav {
  display: flex;
  flex-direction: column;
  background-color: #4e73df;
  background-image: linear-gradient(180deg, #4e73df 10%, #224abe 100%);
  background-size: cover;
  width: 12rem;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .flex .navbar-nav {
    width: auto;
  }
}
.flex .navbar-nav p {
  color: #FFFFFF;
}

.sidebar-brand {
  display: flex;
  padding: 2.4rem 1.6rem;
  justify-content: center;
  align-items: center;
}
.sidebar-brand .sidebar-brand-icon {
  font-size: 3.5rem;
  color: #FFFFFF;
}
.sidebar-brand .sidebar-brand-text {
  display: none;
  transition: 0.3s;
  color: #FFFFFF;
  font-weight: 700;
  margin: 0 1.6rem;
}
@media (min-width: 768px) {
  .sidebar-brand .sidebar-brand-text {
    display: block;
  }
}
.sidebar-brand .sidebar-brand-text p {
  font-size: 1.6rem;
}

.siderbar-divider {
  margin: 0 1.6rem 1.6rem;
}
.siderbar-divider.my-0 {
  margin: 0 1.6rem 0;
}

hr {
  border-top: 1px solid rgba(255, 255, 255, 0.15);
  border-bottom: 1px rgba(255, 50, 50, 0);
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

.nav-item {
  position: relative;
  color: #FFFFFF;
  font-weight: 700;
  height: 2rem;
  height: auto;
}
.nav-item .nav-link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: 0.3s;
  text-align: left;
  padding: 1.6rem;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .nav-item .nav-link {
    display: block;
    width: 25rem;
  }
}
.nav-item .nav-link:hover .bx-icon, .nav-item .nav-link:hover .span, .nav-item .nav-link:visited .bx-icon, .nav-item .nav-link:visited .span, .nav-item .nav-linkfocus .bx-icon, .nav-item .nav-linkfocus .span, .nav-item .nav-link:active .bx-icon, .nav-item .nav-link:active .span {
  color: #FFFFFF;
}
.nav-item .nav-link .bx-icon {
  color: rgba(255, 255, 255, 0.5);
}
.nav-item .nav-link .span {
  color: rgba(255, 255, 255, 0.8);
  font-weight: 200;
}
.nav-item .nav-link .bx-arrow {
  display: none;
  transition: 0.3s;
  color: rgba(255, 255, 255, 0.5);
  margin: 0.5rem;
  float: right;
  vertical-align: 0;
}
@media (min-width: 768px) {
  .nav-item .nav-link .bx-arrow {
    display: block;
  }
}

.sidebar-heading {
  color: rgba(255, 255, 255, 0.4);
  padding: 0 1.6rem;
  font-weight: 800;
  font-size: 1rem;
  text-transform: uppercase;
  text-align: center;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .sidebar-heading {
    text-align: left;
  }
}

.nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.contenedor {
  width: 100%;
  flex-direction: column;
  background-color: #F8F9FC;
}
.contenedor .header {
  margin: 0;
  background-color: #FFFFFF;
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
  height: 7rem;
}

.navbar {
  display: flex;
  justify-content: right;
  align-items: center;
  padding: 0.8rem 1.6rem;
  height: 100%;
  transition: 0.3s;
}
.navbar .navbar-ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.navbar .navbar-ul .bloque-nav {
  border-right: 1px solid #e3e6f0;
  height: 3rem;
  margin: 0 2rem;
  width: 0;
}

.nav-caja {
  overflow: hidden;
  opacity: 0;
  border-radius: 0.5rem;
  background-color: #FFFFFF;
  position: absolute;
  left: 11.75rem;
  z-index: 1;
  top: 4px;
  transition: 0.3s;
}
.nav-caja.mostrar {
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
  opacity: 1;
}
@media (min-width: 768px) {
  .nav-caja {
    left: auto;
    height: 0;
    overflow: hidden;
    margin: 0 2rem 2rem;
    position: relative;
    z-index: 1;
  }
  .nav-caja.mostrar {
    height: 11.5rem;
  }
}

.nav-caja-items {
  padding: 0.5rem 0;
  min-width: 10rem;
  font-size: 1.5rem;
  margin: 0;
  transition: 0.3s;
}
@media (min-width: 768px) {
  .nav-caja-items {
    margin: 0 0 1rem 0;
  }
}
.nav-caja-items h6 {
  margin: 0;
  white-space: nowrap;
  padding: 0.5rem 1.5rem;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 1rem;
  color: #b7b9cc;
}
.nav-caja-items a {
  font-weight: 400;
  padding: 0.5rem 1rem;
  margin: 0 0.5rem;
  display: block;
  color: #3a3b45;
  text-decoration: none;
  border-radius: 0.35rem;
  white-space: nowrap;
}
.nav-caja-items a:hover {
  background-color: #EAECF4;
}

.nav-item-li {
  position: relative;
}
.nav-item-li .nav-link-li {
  height: 4.375rem;
  display: flex;
  align-items: center;
  padding: 0 0.75rem;
  position: relative;
}
.nav-item-li .nav-link-li .bx-nav-icon {
  font-size: 2rem;
  color: #d1d3d2;
}
.nav-item-li .nav-link-li:hover .bx-nav-icon {
  color: #B7B9CC;
}
.nav-item-li .nav-link-li .span-li {
  position: absolute;
  transform: scale(0.7);
  transform-origin: top right;
  right: -0.1rem;
  margin-top: -0.9rem;
  color: #fff;
  background-color: #e74a3b;
  display: inline-block;
  padding: 0.25em 0.4em;
  font-size: 75%;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  white-space: nowrap;
  vertical-align: baseline;
  border-radius: 0.35rem;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.nav-item-li .nav-link-li .img-perfil {
  width: 3.5rem;
  height: 3.5rem;
}
.nav-item-li .nav-link-li .name-link {
  margin: 0 0.8rem 0 0;
  color: #858796;
  font-weight: 400;
}

.caja-perfil {
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  width: 15rem;
  position: absolute;
  right: 0.5rem;
  top: 6rem;
  background-color: #FFFFFF;
  border-radius: 0.5rem;
  box-shadow: 0 0.15rem 1.75rem 0 rgba(58, 59, 69, 0.15);
  padding: 1rem 0;
  transition: 0.3s;
}
.caja-perfil.quitar {
  visibility: hidden;
  opacity: 0;
}
.caja-perfil.mostrar {
  visibility: visible;
  opacity: 1;
}
.caja-perfil .perfil-item {
  display: block;
  padding: 0.4rem 2.3rem;
}
.caja-perfil .perfil-item:hover {
  background-color: #EAECF4;
}
.caja-perfil .perfil-item i {
  color: #d1d3d2;
}
.caja-perfil .perfil-item span {
  font-size: 1.4rem;
  margin-left: 0.5rem;
  color: #3a3b45;
  font-weight: 400;
}

.divider-perfil {
  height: 0;
  margin: 0.5rem 0;
  overflow: hidden;
  border-top: 1px solid #eaecf4;
}

.main {
  height: 90rem;
}